//Controls stepper motor. Stepper between 0 and 20000 steps.

<template>
    <div id='load-control'>
        <div class='d-grid gap-2 d-sm-block mb-2'>
            <button type='button' class='btn btn-sm btn-primary me-2' @click='read'>Start Reading</button>
            <button type='button' class='btn btn-sm btn-danger me-2' @click='standby'>Standby</button>
            <button type='button' class='btn btn-sm btn-secondary me-2' @click='reset'>Reset</button>
            <button type='button' class='btn btn-sm btn-secondary me-2' @click='tare'>Tare</button>

        </div>

        <div class='row'>
            <div class='col-sm-6 mb-2'>
                <!-- <img id='load-cell-image' src='../../public/images/load-cell.jpg' alt='load-cell-image'> -->
                <webcam-stream id='webcam-stream' />
            </div>
            
            <!-- Simple input for load control -->
            <div v-if='controlVersion == 0' class='col-sm-6 btn-group-vertical'>
                <div class="input-group mb-3">
                    <input type="text" id='load-input' :class='getTextClass' aria-describedby="button-addon2" v-model='servo_input' @keyup.enter='setPositionAndMove(servo_input)' @mousedown='setDraggable(false)' @mouseup='setDraggable(true)'>
                    <button :class='getButtonClass' type="button" id="set-pos-button" @submit.prevent @click='setPositionAndMove(servo_input)'>Set</button>
                </div>
            </div>

            <!-- Slider for load control -->
             <div v-else-if='controlVersion == 1' class='col-sm-6 btn-group-vertical'>
                 <div class="input-group mb-3">
                    <input class='form-range m-2' type="range" id='load-input' v-model='servo_input' min='0' max='20000' step='200' @change='setPositionAndMove(servo_input)' @mousedown='setDraggable(false)' @mouseup='setDraggable(true)'>
                    <input :class='getTextClass' type='text' id='load-output' :value='servo_input' readonly>
                
                    <div class='row m-2'>
                        <div class='col-4'><button type='button' class='btn btn-outline-primary btn-sm' @click='incrementMove(-100)'>-</button></div>
                        <div class='col-4'><label type='label' class='col-form-label'>100</label></div>
                        <div class='col-4'><button type='button' class='btn btn-outline-primary btn-sm' @click='incrementMove(100)'>+</button></div>

                        <div class='col-4'><button type='button' class='btn btn-outline-primary btn-sm' @click='incrementMove(-10)'>-</button></div>
                        <div class='col-4'><label class='col-form-label'>10</label></div>
                        <div class='col-4'><button type='button' class='btn btn-outline-primary btn-sm' @click='incrementMove(10)'>+</button></div>

                        <div class='col-4'><button type='button' class='btn btn-outline-primary btn-sm' @click='incrementMove(-1)'>-</button></div>
                        <div class='col-4'><label class='col-form-label'>1</label></div>
                        <div class='col-4'><button type='button' class='btn btn-outline-primary btn-sm' @click='incrementMove(1)'>+</button></div>
                    </div>
                   
                
                
                </div>
                
            </div>

            <!-- Skeumorphic display - "real" control dial -->
            <div v-else-if='controlVersion == 2' class='col-sm-6'>
                <control-dial @onchange='(value) => setPositionAndMove(value)'/>
            </div>

            <!-- Physical controller -->
            <div v-else-if='controlVersion == 4' class='col-sm-6'>
                <game-pad @onchange='(value) => setPositionAndMove(value)' @reset='reset' @tare='tare' />
            </div>

        </div>
    </div>
  
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import ControlDial from './elements/ControlDial.vue';
import WebcamStream from './WebcamStream.vue';
import GamePad from './elements/GamePad.vue';

export default {
    name: 'LoadControl',
    components: {
        ControlDial,
        WebcamStream,
        GamePad,
    },
    props:['controlVersion'],
    data(){
        return {
            servo_input: 0.00,
        }
    },
    mounted(){
        this.servo_input = this.getServoPosition;
    },
    computed:{
        ...mapGetters([
                'getServoPosition',
            ]),
            getTextClass(){
            if(this.getServoPosition == this.servo_input){
                return 'form-control text-center'
            } else {
                return 'form-control text-center text-danger'
            }
        },
        getButtonClass(){
            if(this.getServoPosition == this.servo_input){
                return 'btn btn-secondary'
            } else {
                return 'btn btn-success'
            }
        }
            
    },
    methods:{
        ...mapActions([
            'setDraggable',
            'standby',
            'read',
            'tare',
        ]),
        setPositionAndMove(input){
            console.log('sending to servo = ' + input);
            this.$store.dispatch('setPositionAndMove', input);
        },
        incrementMove(delta){
          this.servo_input = Number(this.servo_input) + Number(delta);
          this.setPositionAndMove(this.servo_input);
        },
        reset(){
            this.$store.dispatch('reset');
            this.servo_input = 0.00;
        },
    }
}
</script>

